import { Component } from '@angular/core';
import { CaseService } from 'src/app/services/case.service';
import { UserService } from '../services/user.service';
import { MessageService } from '../services/message.service';
import { Router, ActivatedRoute } from '@angular/router';
import { DataService } from '../services/data.service';
import { CommonService } from '../services/common.service';

@Component({
  selector: 'app-tabs',
  templateUrl: 'tabs.page.html',
  styleUrls: ['tabs.page.scss']
})
export class TabsPage {
  userInfo: any;
  caseCount = {
    totalActiveCounter: 0
  };
  messageCount = {
    totalUnread: 0 
  };
  message: string;

  constructor(
    public commonService: CommonService,
    public dataService: DataService,
    public route: ActivatedRoute,
    public router: Router,
    public caseService: CaseService,
    public userService: UserService,
    public messageService: MessageService,
  ) {}

  ngOnInit() {
    this.dataService.currentMessage.subscribe(message => this.message = message)

    this.userInfo = this.userService.getUserInfo();
    console.log('this.router :', this.router);
    if (this.userInfo && this.userInfo.id && this.router.url.includes('/tabs')) {
      this.getCaseCount();
      // this.getUnreadCount();
      let terval = setInterval(() => {
        if (this.userInfo && this.userInfo.id && this.router.url.includes('/tabs')) {
          // this.getUnreadCount();
          this.getCaseCount();
        }
        else {
          clearInterval(terval);
        }
      }, this.commonService.intervalTime);
    }
  }

  getCaseCount() {
    this.caseService.getCaseCount(this.userInfo.id)
      .subscribe((res: any) => {
        this.caseCount = res;
      });
  }

  // getUnreadCount() {
  //   this.messageService.getUnreadCount()
  //     .subscribe((res: any) => {
  //       this.messageCount = res;
  //     });
  // }
}
